/* ---------------- General ---------------- */

html {
  background-color: whitesmoke;
}

a + a {
  margin-left: 200px;
}

a {
  text-decoration: none;
  color: white;
}

.app {
  width: 100%;
  height: 100%;
}

/* ---------------- Header ---------------- */

.header-container {
  padding: 10px 0px 10px 10px;
  width: 100%;
  min-width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: whitesmoke;
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
  z-index: 10;
}

.header-link {
  color: black;
  transition: 0.2s;
}

.header-link:hover {
  color: rgb(238, 192, 40);
}

.highlight {
  color: white;
}

/* ---------------- Home ---------------- */

#home-container {
  padding-top: 7%;
  width: 70vw;
  margin: auto;
  text-align: center;
}

#bg-container {
  margin: 0 auto;
  width: 70%;
  height: 60%;
  background-color: whitesmoke;
  position: absolute;
  z-index: -1;
}

#bg-1 {
  width: 50%;
  height: 100%;
  background-color: rgb(132, 179, 182);
  position: relative;
  float: left;
  border-radius: 10px 0px 0px 10px;
  -webkit-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
  -moz-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
  box-shadow: 19px 10px 10px -5px rgba(0, 0, 0, 0.21);
}

#bg-2 {
  width: 50%;
  height: 100%;
  background-color: rgb(236, 205, 67);
  position: relative;
  float: left;
  border-radius: 0px 10px 10px 0px;
  -webkit-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
  -moz-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
  box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
}

#greeting {
  color: black;
  font-family: "Poppins", sans-serif;
  font-size: 45px;
  padding-top: 15%;
  padding-right: 15px;
}

#greeting-intro {
  font-size: 30px;
  font-family: "Josefin Sans", sans-serif;
  color: black;
}

/* ---------------- About me ---------------- */

#about-container {
  width: 60vw;
  margin: auto;
  padding: 20px 0px 50px;
}

#about-title {
  font-size: 40px;
  font-family: "Poppins", sans-serif;
  color: black;
}

#about-section {
  padding: 30px;
  margin: auto;
  text-align: left;
  background-color: rgb(240, 212, 90);
  border-radius: 10px;
  text-align: left;
  -webkit-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
  -moz-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
  box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
}

#about-name {
  font-family: "Poppins", sans-serif;
  color: whitesmoke;
  font-size: 45px;
  text-align: center;
}

#about-me-intro {
  font-family: "Josefin Sans", sans-serif;
  color: black;
}

#selfie {
  width: 200px;
  padding-top: 50px;
  padding-bottom: 40px;
  margin: auto;
}

/* ---------------- Skills ---------------- */

#skills-container {
  width: 60vw;
  margin: auto;
  padding: 20px 0px 50px;
}

#skills-section {
  padding: 30px;
  margin: auto;
  text-align: left;
  background-color: rgb(132, 179, 182);
  border-radius: 10px;
  text-align: left;
  font-family: "Josefin Sans", sans-serif;
  -webkit-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
  -moz-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
  box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
}

#skills-title {
  font-size: 40px;
  font-family: "Poppins", sans-serif;
  color: black;
}

.skill-name {
  margin-top: 40px;
}

/* ---------------- Contact form ---------------- */

#contact-container {
  width: 40vw;
  margin: auto;
  padding: 20px 0px 50px;
}

#contact-title {
  font-size: 40px;
  font-family: "Poppins", sans-serif;
  color: black;
}

#contact-section {
  padding: 30px;
  margin: auto;
  text-align: left;
  background-color: rgb(240, 212, 90);
  border-radius: 10px;
  text-align: left;
  font-family: "Josefin Sans", sans-serif;
  -webkit-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
  -moz-box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.21);
