/* #fname-input, #lname-input, #phone-input, 
#company-input, #email-input {
  width: 50%;
  z-index: -1;
} */

#message-input {
  height: 200px;
}

#contact-submit {
  font-family: "Josefin Sans";
}

/* ---------------- Footer ---------------- */

#footer-container {
  width: 100vw;
  height: 40px;
  position: fixed;
  bottom: 0;
  background-color: whitesmoke;
  font-family: "Josefin Sans", sans-serif;
  font-size: 25px;
}

#social-container {
  padding: 5px 10px 0px 10px;
  margin-left: 10px;
  transition: 0.3s;
  text-align: right;
}

.fa-linkedin {
  transition: 0.2s;
  color: black;
}

.fa-github-square,
.fa-facebook-square {
  margin-left: 0.5em;
  transition: 0.2s;
  color: black;

}

.fa-linkedin:hover,
.fa-github-square:hover,
.fa-facebook-square:hover {
  color: rgb(238, 192, 40);
}

#footer-misc-container {
  text-align: right;
}

/* ---------------- Misc ---------------- */

.spacer {
  width: 100vw;
  height: 35vh;
  color: whitesmoke;
}

#arrow-down-container {
  display: inline-block;
}

#arrow-down {
  width: 50px;
}
